import api from './api'

$(() => {
  $('#login').click(function () {
    $('.tabs-list li').eq(0).click()
  })
  $('#register').click(function () {
    $('.tabs-list li').eq(1).click()
  })

  var app = new Vue({
    el: '#loginForm',
    data() {
      return {
        loginMsg: {
          visible: false,
          type: 'danger',
          text: '',
        },
        registerMsg: {
          visible: false,
          type: 'danger',
          text: '',
        },
        loginPhone: null,
        loginPwd: null,
        registerPhone: null,
        registerCode: null,
        registerPwd:null,
        registerRepwd: null
      }
    },
    methods: {
      toggleMsg(el, text) {
        el.visible = true
        el.text = text
        el = {
          visible: true,
          type: 'danger',
          text: false
        }

        setTimeout(() => {
          this.loginMsg.visible = false
          this.registerMsg.visible = false
        }, 2500)
      },
      login() {
        api.usercenter
        .login(this.loginPhone, this.loginPwd)
        .then((res) => {
          if (res.code!=='0000') {
            this.toggleMsg(this.loginMsg, res.message)
          } else {
            window.localStorage.setItem('token', res.data.token)
          }
        })
      },
      signup() {
        api.usercenter
        .register(this.registerPhone, this.registerCode, this.registerPwd)
        .then((res) => {
          if (res.code!=='0000') {
            this.toggleMsg(this.registerMsg, res.message)
          } else {
            window.localStorage.setItem('token', res.data.token)
          }
        })
      },
      getCode() {
        api.usercenter.sendCode(this.registerPhone)
      }
    }
  })
});



