import Swiper from 'swiper';

$(() => {
  if($('.car-slider').length < 0) {
    return
  }
  const viewSwiper = new Swiper('.view .swiper-container', {
    on: {
      slideChangeTransitionStart: () => {
        updateNavPosition()
      }
    }
  })
  const previewSwiper = new Swiper('.preview .swiper-container', {
    // visibilityFullFit: true,
    slidesPerView: 'auto',
    allowTouchMove: false,
    on: {
      tap: () => {
        viewSwiper.slideTo(previewSwiper.clickedIndex)
      }
    }
  })

  function updateNavPosition() {
    $('.preview .active-nav').removeClass('active-nav');
    const activeNav = $('.preview .swiper-slide')
      .eq(viewSwiper.activeIndex)
      .addClass('active-nav')
    if (!activeNav.hasClass('swiper-slide-visible')) {
      if (activeNav.index() > previewSwiper.activeIndex) {
        const thumbsPerNav =
          Math.floor(previewSwiper.width / activeNav.width()) - 1
        previewSwiper.slideTo(activeNav.index() - thumbsPerNav)
      } else {
        previewSwiper.slideTo(activeNav.index())
      }
    }
  }

  $('.preview .arrow-left').on('click', function(e) {
    e.preventDefault()
    if (viewSwiper.activeIndex === 0) {
      viewSwiper.slideTo(viewSwiper.slides.length - 1, 1000)
      return
    }
    viewSwiper.slidePrev()
  })

  $('.preview .arrow-right').on('click', function(e) {
    e.preventDefault()
    if (viewSwiper.activeIndex === viewSwiper.slides.length - 1) {
      viewSwiper.slideTo(0, 1000)
      return
    }
    viewSwiper.slideNext()
  })
})



