import axios from 'axios';
var instance = axios.create({
  baseURL: 'http://localhost:8888/api',
})
var whiteList = [
  '/UserLogin/user_login',
  '/UserReg/regcode',
  '/UserReg/reg'
]

instance.interceptors.request.use(function(config){
  console.log(whiteList.indexOf(config.url.split(config.baseURL)[1]))
  if(window.localStorage.getItem('token') && whiteList.indexOf(config.url)<0){
    if(!config.params) {
      config.params = {}
    }
    config.params['token'] = window.localStorage.getItem('token')
  }
  return config
})

instance.interceptors.response.use(function(res){
  return res.data
})

export default {
  usercenter: {
    // 登录
    login(mobile, password) {
      return instance.post('/UserLogin/user_login', {
        mobile,
        password
      })
    },
    // 验证码
    sendCode(mobile) {
      return instance.post('/UserReg/regcode', {
        mobile
      })
    },
    // 注册
    register(mobile, code, password) {
      return instance.post('/UserReg/reg', {
        mobile,
        code,
        password
      })
    },
    getOrders(type, page=1) {
      return instance.post('/Order/userOrder', {}, {
        params: {
          page: 1,
          order_type: type
        }
      })
    },
    getAfterOrders() {
      return instance.post('/AfterSale/index')
    },
    checkin() {
      return instance.post('/users/sign_in') 
    },
    getUserInfo() {
      return instance.post('/users/userinfo') 
    }
  },

  
}


